@import url(https://fonts.googleapis.com/css2?family=Playfair+Display+SC:ital,wght@1,900&family=Titillium+Web:wght@600&display=swap);
html{overflow:-moz-scrollbars-none;scrollbar-width:none}body{margin:0;background-color:#333333;color:white;font-size:1.2rem;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}.outer-content{padding-top:110px;height:100vh}@media screen and (max-width: 800px){.outer-content{padding-top:100px}}.inner-content{height:83vh;border:2px dashed #c9c9c9;border-radius:50px}@media screen and (max-width: 800px){.inner-content{height:79vh}}#progressBarContainer{position:fixed;z-index:10;background:rgba(255,255,255,0.05);width:100%;top:4.8rem;left:0;height:0.4rem}@media screen and (max-width: 800px){#progressBarContainer{top:4.39rem}}#progressBar{background:#f43aed;background:linear-gradient(56deg, #f43aed 0%, rgba(179,0,249,0.860458) 100%);transform-origin:top left;transform:scale(0, 0);opacity:0;height:0.2rem}.heading-text{padding:4rem 2rem 1.5rem 2rem}.heading-text u{padding-bottom:10px;text-decoration:none;border-bottom:3px solid #c9c9c9}@media screen and (max-width: 800px){.heading-text{font-size:medium;padding:2rem 2rem 1rem 2rem}}.icons{text-decoration:none;color:white;padding:1rem 2rem 1.5rem 2rem}.icons:hover{color:#CF9FFF}@media screen and (max-width: 800px){.icons{font-size:large;padding:0px .1rem}}@media screen and (max-width: 1200px){.icons{padding:.5rem .5rem}}.highlight{color:#9370DB;font-weight:bold;letter-spacing:.1rem}#homepage .animoji-image-div{padding:.5rem}@media screen and (max-width: 800px){#homepage .animoji-image-div{height:13rem}}#homepage .animoji-image-div img{height:13rem}@media screen and (max-width: 800px){#homepage .animoji-image-div img{height:80%}}#homepage .animoji-image{padding:1rem}@media screen and (max-width: 800px){#homepage .animoji-image{height:15rem;padding-bottom:.5rem}}#homepage .icon-div{padding:1.5rem 2rem 2rem 2rem}@media screen and (max-width: 800px){#homepage .icon-div{padding:1rem}}#homepage .icon-div a{padding:auto}#homepage .icon-div span{display:none}#homepage .homepage-text{font-family:'Titillium Web', sans-serif;letter-spacing:0.1rem;padding:.5rem 1rem 2rem 1rem}@media only screen and (min-width: 320px) and (max-width: 568px){#homepage .homepage-text{letter-spacing:normal;font-size:medium;padding:1rem 1.3rem 1.5rem 1.3rem}}@media screen and (max-width: 800px) and (min-width: 330px) and (max-height: 1200px){#homepage .homepage-text{padding:4.2rem 1rem .5rem 1rem;letter-spacing:normal;font-size:medium}}#homepage .homepage-text .typewritter{font-family:'Titillium Web', sans-serif;font-weight:700;letter-spacing:.2rem;color:#9370DB}@media screen and (max-width: 800px){#homepage .homepage-text .typewritter{font-size:medium;letter-spacing:normal}}#homepage .homepage-text .my-name strong{font-family:'Playfair Display', serif;font-size:2rem;letter-spacing:.1rem;color:#9370DB}@media screen and (max-width: 800px){#homepage .homepage-text .my-name strong{font-size:1.2rem}}#about .aboutme{padding:4rem 4rem 4rem 4rem}@media screen and (max-width: 800px){#about .aboutme{padding:3rem;font-size:small}}#resume .resume-box{padding:0rem 1rem .5rem 1rem}@media screen and (max-width: 400px){#resume .resume-box{padding:4rem 0rem 7rem 0rem}}@media screen and (max-width: 400px) and (max-height: 800px){#resume .resume-box{padding-bottom:4rem}}#resume .icon{padding-left:2rem;text-decoration:none;color:white}#resume .icon:hover{color:#CF9FFF}@media screen and (max-width: 800px){#resume .icon{font-size:large}}#skills .skills-container{padding:5rem}.footer-box{color:#c9c9c9;margin-top:2rem;padding:2rem 2rem 1rem 2rem}@media screen and (max-width: 800px){.footer-box{padding:.5rem}}.footer-box p:hover{color:#CF9FFF;cursor:pointer}@media screen and (max-width: 800px){.footer-box p{font-size:small}}

